import * as React from 'react';
import clsx from 'clsx';
import { withStyles, WithStyles } from '@material-ui/core';
import LazyLoad from 'react-lazyload';
import get from 'lodash/get';
import FiberManualRecordOutlinedIcon from '@material-ui/icons/FiberManualRecordOutlined';
import FiberManualRecordIcon from '@material-ui/icons/FiberManualRecord';
import ArrowForwardIcon from '@material-ui/icons/ArrowForward';
import Link from '@material-ui/core/Link';
import { Link as RouterLink } from 'react-router-dom';
import Loadable from '@loadable/component';
import { Resources } from '../../constants/resources';
import Style from './style';

const DesktopCard = Loadable(() => import('../../components/DesktopCard'));

interface LinkType {
	link: string;
	linkText: string;
	externalLink: boolean;
}
export interface Props extends WithStyles<typeof Style> {
	image: string;
	heading: string;
	name?: string;
	content: string | React.ReactNode;
	link?: LinkType | any;
	padding?: string;
	leftContent?: boolean;
}
/**
 * ImageContent for founders section.
 * @param {any} props - Properties
 * @returns {React.Component} component.
 */
class ImageContent extends React.PureComponent<Props> {
	componentDidMount() {}

	getLink = (linkObj) => {
		const { classes } = this.props;
		const link = get(linkObj, 'link', '');
		const externalLink = get(linkObj, 'externalLink', '');
		const linkText = get(linkObj, 'linkText', '');
		const leftContent = get(this.props, 'leftContent', false);
		return link === '' ? null : (
			<div>
				<Link
					key={link}
					to={link}
					href={externalLink ? link : ''}
					target={externalLink ? '__blank__' : ''}
					color="inherit"
					component={externalLink ? 'a' : (RouterLink as any)}
					className={clsx(classes.link, { [classes.flexEnd]: leftContent })}
				>
					<span>{linkText}</span>
					<ArrowForwardIcon className={classes.arrow} />
				</Link>
			</div>
		);
	};

	/**
	 * Render method
	 * @returns {JSX.Element} Jsx.
	 */
	public render() {
		const { classes } = this.props;
		const image = get(this.props, 'image', '');
		const heading = get(this.props, 'heading', '');
		const name = get(this.props, 'name', '');
		const content = get(this.props, 'content', '');
		const link = get(this.props, 'link', {});
		const padding = get(this.props, 'padding', '80px 32px');
		const leftContent = get(this.props, 'leftContent', false);
		return (
			<div
				className={clsx(classes.founderContainer, { [classes.flexStart]: leftContent })}
				style={{ padding }}
			>
				<div className={clsx(classes.outline, { [classes.borderRight]: leftContent })} />
				<div
					className={clsx(classes.founderContent, { [classes.reverseRow]: leftContent })}
				>
					<div
						className={clsx({
							[classes.imageContainer]: !leftContent,
							[classes.imageContainerRight]: leftContent
						})}
					>
						<DesktopCard image={image} small />
						<FiberManualRecordIcon
							className={clsx({
								[classes.dot]: !leftContent,
								[classes.rightDot]: leftContent
							})}
						/>
					</div>
					<div className={classes.textContent}>
						<div className={classes.head}>{heading}</div>
						<div className={classes.largeFont}>{name}</div>
						<p className={classes.body}>{content}</p>
						{this.getLink(link)}
					</div>
				</div>
			</div>
		);
	}
}

export default withStyles(Style, { name: 'DmartCorpFreshImageContent' })(ImageContent);
